html,
body {
  height: 100%;
  color: #61dafb;
  background: #2d2d2d;
}

h1 {
  margin: 20px 0 15px 0;
}

#main {
  width: 600px;
  margin: 0 auto;
  text-align: left;
}

ul {
  list-style: none;
}

ul > li:hover {
  background-color: #f5f5f53a;
}

.form-inline .form-control {
  width: 50%;
  margin-left: 80px;
}

.done {
  color: red;
  text-decoration: line-through;
}

.undone {
  color: #61dafb;
}

input[type="text"], input[type="password"] {
  min-width: 30%;
}
